import React from "react"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome"
import {faEnvelope} from "@fortawesome/free-solid-svg-icons"
import {faLinkedin} from "@fortawesome/free-brands-svg-icons"

export default function Info() {
    return(
        <div className="info">
            <div className="info--img">
            </div>
            <h1 className="info--h1">Alan Kelly</h1>
            <h4 className="info--jobtitle">Frontend Developer</h4>
            <a href="https://github.com/xlan11" className="info--email">github.com/xlan11</a>
            <div className="btn--container">
                <button className="btn btn--email"><a href="mailto:#@#.com" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faEnvelope}></FontAwesomeIcon> Email</a></button>
                <button className="btn btn--linkedin"><a href="https://www.linkedin.com/in/alan-kelly-16280a1b/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faLinkedin}></FontAwesomeIcon> LinkedIn </a></button>
            </div>
        </div>
    )
}