
import React from 'react'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome"
import {faTwitterSquare, faHashnode, faInstagramSquare, faGithubSquare} from "@fortawesome/free-brands-svg-icons"

export default function Footer() {
    return (
        <div className="footer">
            <div className="footer--item"><a href="https://twitter.com/akelly_dev" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faTwitterSquare}></FontAwesomeIcon></a></div>
            <div className="footer--item"><a href="https://akelly.hashnode.dev/" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faHashnode}></FontAwesomeIcon></a></div>
            <div className="footer--item"><a href="https://instagram.com/xlankelly" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faInstagramSquare}></FontAwesomeIcon></a></div>
            <div className="footer--item"><a href="https://github.com/xlan11" target="_blank" rel="noopener noreferrer"><FontAwesomeIcon icon={faGithubSquare}></FontAwesomeIcon></a></div>    
        </div>
    )
}